let funcionesComunes = {

    convertirNumeroAEstrellas(numero) {
        let stringEstrellas = ""
        for (let i = 0; i < numero; i++) { stringEstrellas += this.icono + " " }
        if (numero - parseInt(numero) > 0.25) stringEstrellas += this.medioicono;
        return stringEstrellas;
    }


}